import React, { useState, useEffect } from 'react';
import { TonConnectUI, THEME, SendTransactionRequest } from '@tonconnect/ui';
import './styles.css';
import domains from './domains';

const tonConnectUI = new TonConnectUI({
  manifestUrl: 'https://liomlong.github.io/pigminiapp/tonconnect-manifest.json',
  uiPreferences: {
    theme: THEME.DARK,
    language: 'en'
  }
});

const RECEIVER_ADDRESS = 'UQC4dyfbdE9SPsgYphA18gSYcNuqqxTqHGKvvJPKTgEERjew';  // 你的接收地址

function App() {
  const [connected, setConnected] = useState(false);
  const [userAddress, setUserAddress] = useState(localStorage.getItem('userAddress') || null);

  useEffect(() => {
    const unsubscribe = tonConnectUI.onStatusChange((wallet) => {
      if (wallet) {
        setConnected(true);
        setUserAddress(wallet.account.address);
        localStorage.setItem('userAddress', wallet.account.address);
      } else {
        setConnected(false);
        setUserAddress(null);
        localStorage.removeItem('userAddress');
      }
    });
    return () => unsubscribe();
  }, []);

  const handleConnect = async () => {
    await tonConnectUI.connectWallet();
  };

  const handleBuy = async (domain) => {
    if (connected) {
      const transaction = {
        validUntil: Math.floor(Date.now() / 1000) + 3600,  // 1小时
        messages: [
          {
            address: RECEIVER_ADDRESS,
            amount: '40000000000',  // 40 TON, 以nanoton为单位
            payload: `Buying domain: ${domain.name}`
          }
        ]
      };

      try {
        const result = await tonConnectUI.sendTransaction(transaction);
        alert('Payment successful!');

        // 记录用户地址和购买的域名
        console.log(`User ${userAddress} bought ${domain.name}`);
        // 在这里处理购买后的逻辑，例如更新域名状态
      } catch (error) {
        console.error('Payment failed', error);
        alert('Payment failed');
      }
    } else {
      alert('Please connect your wallet first.');
    }
  };

  return (
    <div className="app">
      <h1>Pig Mini App - Domain Sales</h1>
      <button onClick={handleConnect} className="connect-button">
        {connected ? `Connected: ${userAddress}` : 'Connect to TON Wallet'}
      </button>
      {userAddress && <p>Your Address: {userAddress}</p>}
      <div className="domain-list">
        {domains.map((domain) => (
          <div key={domain.name} className="domain-item">
            <span>{domain.name}</span>
            <span>{domain.sold ? 'Sold' : '40 TON'}</span>
            <button
              onClick={() => handleBuy(domain)}
              className={domain.sold ? 'sold' : 'buy'}
              disabled={domain.sold}
            >
              {domain.sold ? 'Sold' : 'Buy'}
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}

export default App;
